import { Component, OnInit } from '@angular/core';
import { AppService } from '../../shared/services/service';
import { Router,ActivatedRoute } from '@angular/router';
import {ToasterService} from '../../shared/services/toaster.service';
import { LoaderService } from 'app/shared/services/loader.service';
import { CookieService } from 'ngx-cookie';
@Component({
    selector: 'app-socialpost',
    templateUrl: './socialpost.component.html',
    styleUrls: ['./socialpost.component.scss']
})

export class SocialpostComponent implements OnInit {
    Socialpostdata: any;
    constructor(private appservice: AppService,private router: Router, private route: ActivatedRoute, private toasterservice: ToasterService, private loaderservice: LoaderService, private cookieservice: CookieService ){}
    ngOnInit() {
        if(!this.cookieservice.get("loginuserMerck")){
            this.router.navigate(['/auth']);
        }
        this.getSocialdata();
    }

    getSocialdata() {
        this.appservice.socialpostdata()
            .subscribe(
                data => {
                   // console.log(data.data.socialpostinfo);
                    if(data.status=='1'){
                        this.Socialpostdata = data.data.socialpostinfo;
                    }
                }
            );
    }
}
