import { Component, OnInit } from '@angular/core';
import {AppService} from '../../shared/services/service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import {ActivatedRoute ,Router} from '@angular/router';
import {ToasterService} from '../../shared/services/toaster.service';
import { LoaderService } from 'app/shared/services/loader.service';
import { CookieService } from 'ngx-cookie';

@Component({
    selector: 'app-event',
    templateUrl: './create-event.component.html',
    styleUrls: ['./create-event.component.scss']
})

export class CreateEventComponent implements OnInit{
    showbutton: boolean;
    id: any;
    EventForm: FormGroup;
    showErrorMsg: string;
    Sluglist: any;
    public editorValue: string = '';
    file: any;
    fileName: any;
    name: any;
    descen: any;
    exdate:any;
    eventdate:any;
    constructor(private appservice: AppService,private router: Router, private route: ActivatedRoute, private toasterservice: ToasterService, private loaderservice: LoaderService, private cookieservice: CookieService  ){}
    
    ngOnInit(){
        if(!this.cookieservice.get("loginuserMerck")){
            this.router.navigate(['/auth']);
        }
        this.id = this.route.snapshot.paramMap.get('id');
        if(this.id){
            this.geteventdata();
            this.showbutton = true;
            this.descen="";
            this.exdate=new Date();
            console.log(this.id);
            this.EventForm = new FormGroup({
                title: new FormControl("", [Validators.required]),
                description: new FormControl("", [Validators.required]),
                event_date: new FormControl(""),
                image: new FormControl(""),
            });
        }
    }
    geteventdata(){
        this.appservice.EventDataid(this.id)
          .subscribe(
              data=>{
                  console.log(data);
                  if(data.status=='1')
                  {
                      this.showErrorMsg = "";
                      //this.descen=data.data[0];
                      this.exdate=data.data.eventinfoid[0].event_date;
                      this.descen=data.data.eventinfoid[0].image;
                      this.EventForm = new FormGroup({
                        title: new FormControl(data.data.eventinfoid[0].title, [Validators.required]),
                        description: new FormControl(data.data.eventinfoid[0].description, [Validators.required]),
                        event_date: new FormControl(data.data.eventinfoid[0].event_date),
                        image: new FormControl(data.data.eventinfoid[0].image, []),
                      });
                      
                  }else{
                      this.showbutton = false;
                  }
              }
          );
    }

    filechange(e){
        this.file = e.target.files[0];
        console.log(this.file);
        this.fileName = e.target.files[0];
        this.name = e.target.files[0].name;
    }
    createeventdata(image){
        if(this.EventForm.invalid){
            return;
        }    
        if(this.EventForm.valid){
            console.log(this.EventForm);
            this.loaderservice.display(true);
            this.eventdate=document.getElementById("eventdate");
            this.appservice.addEventdata( this.EventForm.value, this.fileName,this.eventdate.value)
            .subscribe(
            data=>{
                console.log(data);
                if(data.status=='1')
                {
                    this.toasterservice.Success(data.message);   
                    this.router.navigate(['/upcoming']);                
                }else if(data.status=='2'){
                    this.toasterservice.Error(data.message);
                    this.router.navigate(['/upcoming']);  
                }else{
                    this.toasterservice.Error(data.message);
                }
            }
        );
            //console.log(this.UserForm.value);
        }
    }
    editeventdata(){
        if(this.EventForm.invalid){
            return;
        }    
        if(this.EventForm.valid){
            this.loaderservice.display(true);
            this.eventdate=document.getElementById("eventdate");
            this.appservice.editeventdata(this.EventForm.value,this.id,this.fileName,this.eventdate.value)
            .subscribe(
                data=>{
                    console.log(data);
                    if(data.status=='1')
                    {
                        this.toasterservice.Success(data.message);   
                        this.router.navigate(['/upcoming']);                
                    }else if(data.status=='2'){
                        this.toasterservice.Error(data.message);
                        this.router.navigate(['/upcoming']);  
                    }else{
                        this.toasterservice.Error(data.message);
                    }
                }
            );
        }
    }
    Gotolist(){
        this.router.navigate(['/upcoming']);
    }

}


