import { Component, OnInit } from '@angular/core';
import { AppService } from '../../shared/services/service';
import { Router,ActivatedRoute } from '@angular/router';
import {ToasterService} from '../../shared/services/toaster.service';
import { LoaderService } from 'app/shared/services/loader.service';
import { CookieService } from 'ngx-cookie';
@Component({
    selector: 'app-post',
    templateUrl: './post.component.html',
    styleUrls: ['./post.component.scss']
})

export class PostComponent implements OnInit {
    Postdata: any;
    constructor(private appservice: AppService,private router: Router, private route: ActivatedRoute, private toasterservice: ToasterService, private loaderservice: LoaderService, private cookieservice: CookieService ){}
    ngOnInit() {
        if(!this.cookieservice.get("loginuserMerck")){
            this.router.navigate(['/auth']);
        }
        this.getPostdata();
    }

    getPostdata() {
        this.appservice.postdata()
            .subscribe(
                data => {
                   // console.log(data.data.postinfo);
                    if(data.status=='1'){
                        this.Postdata = data.data.postinfo;
                    }
                }
            );
    }
    goToCreatedata(id) {
        // alert();
        this.router.navigate(['/create-post',id]);
    }
    deletePostdata(id){
        this.appservice.deletepost(id)
            .subscribe(
                data => {
                    console.log(data.data.postinfo);
                    if(data.status=='1')
                    {
                        this.Postdata = data.data.postinfo;
                        this.toasterservice.Success(data.message);   
                        this.router.navigate(['/post']);                
                    }else if(data.status=='2'){
                        this.toasterservice.Error(data.message);
                        this.router.navigate(['/post']);  
                    }else{
                        this.toasterservice.Error(data.message);
                    }
                }
            );
    }
    posttype(id,feature){
        this.appservice.posttype(id,feature)
            .subscribe(
                data => {
                    console.log(data.data.postinfo);
                    if(data.status=='1')
                    {
                        this.Postdata = data.data.postinfo;
                        this.toasterservice.Success(data.message);   
                        this.router.navigate(['/post']);                
                    }else if(data.status=='2'){
                        this.toasterservice.Error(data.message);
                        this.router.navigate(['/post']);  
                    }else{
                        this.toasterservice.Error(data.message);
                    }
                }
            );
    }

}
