import { Component, ViewChild } from '@angular/core';
import { AppService } from '../../shared/services/service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import {Router} from '@angular/router';
import { CookieService } from 'ngx-cookie';

@Component({
    selector: 'app-appuser',
    templateUrl: './appuser.component.html',
    styleUrls: ['./appuser.component.scss']
})

export class AppuserComponent {
    userData: any;
    UserSearch :FormGroup;
    totalRecords: any;
    initialPageValue = 1;
    startpagevalue = 0;
    pno:any;
    itemsPerPage:any;
    currentPage:any;
    totalrecord:any;

    constructor(private appservice: AppService, private route: Router, private cookieservice: CookieService){
    }

    ngOnInit(){
        if(!this.cookieservice.get("loginuserMerck")){
            this.route.navigate(['/auth']);
        }
        this.getUsers();
        this.UserSearch = new FormGroup({
            search: new FormControl("", [Validators.required]),
        });
    }

    getUsers(){
        this.appservice.users(this.startpagevalue,10)
        .subscribe(
            data=>{
                if(data.status=='1'){
                    console.log(data);
                    this.userData = data.data.userinfo;
                    this.totalRecords = data.record;
                    this.itemsPerPage =10;
                    this.currentPage =this.initialPageValue;
                }
            }
        );
        }

        goToCreateUser(id){
           // alert();
          this.route.navigate(['/create-user', id]);
        }

        handleChange(en){
            this.pno=en-1;
            this.appservice.users(this.pno,10)
            .subscribe(
                data => {
                    if(data.status=='1'){
                        this.userData = data.data.userinfo;
                        this.totalRecords = data.record;
                        this.itemsPerPage =10;
                        this.currentPage =en;
                    }
                }
            );
        }
        getrecord(){
            this.totalrecord=document.getElementById("getpage");
            this.appservice.users(this.startpagevalue,this.totalrecord.value)
            .subscribe(
                data => {
                    if(data.status=='1'){
                        this.userData = data.data;
                        if(this.totalrecord.value == 10){
                            this.totalRecords = data.record;
                        }else{
                            this.totalRecords = '';
                        }
                        this.itemsPerPage =10;
                        this.currentPage =this.initialPageValue;
                    }
                }
            );
        }
        servicetype(id,feature){
            this.totalrecord=document.getElementById("getpage");
            this.appservice.servicetype(this.startpagevalue,this.totalrecord.value,id,feature)
            .subscribe(
                data => {
                    if(data.status=='1'){
                        this.userData = data.data;
                        if(this.totalrecord.value == 10){
                            this.userData = data.data.userinfo;
                            this.totalRecords = data.record;
                            this.itemsPerPage =10;
                            this.currentPage =this.initialPageValue;
                        }else{
                            this.totalRecords = '';
                        }
                        this.itemsPerPage =10;
                        this.currentPage =this.initialPageValue;
                    }
                }
            );
        }
}