import { Component, OnInit } from '@angular/core';
import { AppService } from '../../shared/services/service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router,ActivatedRoute } from '@angular/router';
import {ToasterService} from '../../shared/services/toaster.service';
import { LoaderService } from 'app/shared/services/loader.service';
import { CookieService } from 'ngx-cookie';

@Component({
    selector: 'app-upcoming',
    templateUrl: './upcoming.component.html',
    styleUrls: ['./upcoming.component.scss'],
})

export class UpcomingComponent implements OnInit {
    upcomingevent: any;
    EventSearch :FormGroup;
    totalRecords: any;
    initialPageValue = 1;
    startpagevalue = 0;
    pno:any;
    itemsPerPage:any;
    currentPage:any;
    totalrecord:any;
    constructor(private appservice: AppService,private router: Router, private route: ActivatedRoute, private toasterservice: ToasterService, private loaderservice: LoaderService, private cookieservice: CookieService ){}
    ngOnInit() {
        if (!this.cookieservice.get("loginuserMerck")) {
            this.router.navigate(['/auth']);
        }
        this.getupcoming();
        this.EventSearch = new FormGroup({
            search: new FormControl("", [Validators.required]),
        });
    }

    getupcoming() {
        this.appservice.upcoming(this.startpagevalue,10)
            .subscribe(
                data => {
                    if (data.status == '1') {
                        this.upcomingevent = data.data.upcomingevent;
                        this.totalRecords = data.record;
                        this.itemsPerPage =10;
                        this.currentPage =this.initialPageValue;
                    }
                }
            );
    } 
    deletecomingevent(id){
        this.appservice.deleteupcoming(id)
            .subscribe(
                data => {
                    console.log(data);
                    if(data.status=='1')
                    {
                        this.upcomingevent = data.data.upcomingevent;
                        this.toasterservice.Success(data.message);   
                        this.router.navigate(['/upcoming']);                
                    }else if(data.status=='2'){
                        this.toasterservice.Error(data.message);
                        this.router.navigate(['/upcoming']);  
                    }else{
                        this.toasterservice.Error(data.message);
                    }
                }
            );
    }
    goToCreatecomingevent(id) {
        // alert();
        this.router.navigate(['/create-event', id]);
    }
    goToaddevent(id) {
        // alert();
        this.router.navigate(['/create-event', id]);
    }
    handleChange(en){
        this.pno=en-1;
        this.appservice.upcoming(this.pno,10)
        .subscribe(
            data => {
                if(data.status=='1'){
                    console.log(data.data.upcomingevent);
                    this.upcomingevent = data.data.upcomingevent;
                    this.totalRecords = data.record;
                    this.itemsPerPage =10;
                    this.currentPage =en;
                }
            }
        );
    }
    getrecord(){
        this.totalrecord=document.getElementById("getpage");
        this.appservice.upcoming(this.startpagevalue,this.totalrecord.value)
        .subscribe(
            data => {
                if(data.status=='1'){
                    this.upcomingevent = data.data.upcomingevent;
                    if(this.totalrecord.value == 10){
                        this.totalRecords = data.record;
                    }else{
                        this.totalRecords = '';
                    }
                    this.itemsPerPage =10;
                    this.currentPage =this.initialPageValue;
                }
            }
        );
    }

}
