import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { Dashboard1Component } from "./dashboard1/dashboard1.component";
import {AppuserComponent} from './appuser/appuser.component';
import {CreateUserComponent} from './create-user/create-user.component';
import {PostComponent} from './post/post.component';
import {CreatePostComponent} from './create-post/create-post.component';
import {CategoryComponent} from './category/category.component';
import {CreateCategoryComponent} from './create-category/create-category.component';
import {BookingComponent} from './booking/booking.component';
import {PaymentComponent} from './payment/payment.component';
import {BookingstatusComponent} from './bookingstatus/bookingstatus.component';
import {FollowComponent}from './follow/follow.component';
import {SocialpostComponent} from './socialpost/socialpost.component';
import {UpcomingComponent} from './upcoming/upcoming.component';
import {CreateEventComponent} from './create-event/create-event.component';

const routes: Routes = [
  {
    path: '',
    children: [
     
      {
        path: 'upcoming',
        component: UpcomingComponent,
        data: {
          title: 'Upcoming'
        }
      },
      {
        path: 'dashboard',
        component: Dashboard1Component,
        data: {
          title: 'Dashboard'
        }
      },
      {
        path: 'appuser',
        component: AppuserComponent,
        data: {
          title: 'App user'
        }
      },
      {
        path: 'create-user/:id',
        component: CreateUserComponent,
        data: {
          title: 'Create User'
        }
      },
      {
        path: 'create-event/:id',
        component: CreateEventComponent,
        data: {
          title: 'Create Event'
        }
      },
      {
        path: 'post',
        component: PostComponent,
        data: {
          title: 'Post list'
        }
      },
      {
        path: 'create-post/:id',
        component: CreatePostComponent,
        data: {
          title: 'Add Post'
        }
      }, 
      {
        path: 'category',
        component: CategoryComponent,
        data: {
          title: 'Category list'
        }
      },
      {
        path: 'create-category/:id',
        component: CreateCategoryComponent,
        data: {
          title: 'Add Category'
        }
      }, 
      { 
        path: 'booking',
        component: BookingComponent,
        data: {
          title: 'Booking'
        }
      },
      { 
        path: 'bookingstatus',
        component: BookingstatusComponent,
        data: {
          title: 'Booking Status'
        }
      }, 
      { 
        path: 'payment',
        component: PaymentComponent,
        data: {
          title: 'Payment'
        }
      },  
      { 
        path: 'follow',
        component: FollowComponent,
        data: {
          title: 'Follow'
        }
      },
      { 
        path: 'socialpost',
        component: SocialpostComponent,
        data: {
          title: 'Social Post'
        }
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class DashboardRoutingModule { }
