import { Component, OnInit } from '@angular/core';
import { AppService } from '../../shared/services/service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router,ActivatedRoute } from '@angular/router';
import {ToasterService} from '../../shared/services/toaster.service';
import { LoaderService } from 'app/shared/services/loader.service';
import { CookieService } from 'ngx-cookie';

@Component({
    selector: 'app-category',
    templateUrl: './category.component.html',
    styleUrls: ['./category.component.scss'],
})

export class CategoryComponent implements OnInit {
    categorydata: any;
    CategorySearch :FormGroup;
    totalRecords: any;
    initialPageValue = 1;
    startpagevalue = 0;
    pno:any;
    itemsPerPage:any;
    currentPage:any;
    totalrecord:any;
    constructor(private appservice: AppService,private router: Router, private route: ActivatedRoute, private toasterservice: ToasterService, private loaderservice: LoaderService, private cookieservice: CookieService ){}
    ngOnInit() {
        if (!this.cookieservice.get("loginuserMerck")) {
            this.router.navigate(['/auth']);
        }
        this.getcategory();
        this.CategorySearch = new FormGroup({
            search: new FormControl("", [Validators.required]),
        });
    }

    getcategory() {
        this.appservice.category(this.startpagevalue,10)
            .subscribe(
                data => {
                    if (data.status == '1') {
                        this.categorydata = data.data.category;
                        this.totalRecords = data.record;
                        this.itemsPerPage =10;
                        this.currentPage =this.initialPageValue;
                    }
                }
            );
    } 
    deletecategory(id){
        this.appservice.deletecategory(id)
            .subscribe(
                data => {
                    console.log(data);
                    if(data.status=='1')
                    {
                        this.categorydata = data.data.category;
                        this.toasterservice.Success(data.message);   
                        this.router.navigate(['/category']);                
                    }else if(data.status=='2'){
                        this.toasterservice.Error(data.message);
                        this.router.navigate(['/category']);  
                    }else{
                        this.toasterservice.Error(data.message);
                    }
                }
            );
    }
    goToCreatecategory(id) {
        // alert();
        this.router.navigate(['/create-category', id]);
    }
    goToaddcategory(id) {
        // alert();
        this.router.navigate(['/create-category', id]);
    }
    handleChange(en){
        this.pno=en-1;
        this.appservice.category(this.pno,10)
        .subscribe(
            data => {
                if(data.status=='1'){
                    console.log(data.data.category);
                    this.categorydata = data.data.category;
                    this.totalRecords = data.record;
                    this.itemsPerPage =10;
                    this.currentPage =en;
                }
            }
        );
    }
    getrecord(){
        this.totalrecord=document.getElementById("getpage");
        this.appservice.category(this.startpagevalue,this.totalrecord.value)
        .subscribe(
            data => {
                if(data.status=='1'){
                    this.categorydata = data.data.category;
                    if(this.totalrecord.value == 10){
                        this.totalRecords = data.record;
                    }else{
                        this.totalRecords = '';
                    }
                    this.itemsPerPage =10;
                    this.currentPage =this.initialPageValue;
                }
            }
        );
    }

}
