import { Component, ViewChild } from '@angular/core';
import { AppService } from '../../shared/services/service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import {Router} from '@angular/router';
import { CookieService } from 'ngx-cookie';

@Component({
    selector: 'app-payment',
    templateUrl: './payment.component.html',
    styleUrls: ['./payment.component.scss']
})

export class PaymentComponent {
    paymentData: any;
    PayementSearch :FormGroup;
    totalRecords: any;
    initialPageValue = 1;
    startpagevalue = 0;
    pno:any;
    itemsPerPage:any;
    currentPage:any;
    totalrecord:any;
  

    constructor(private appservice: AppService, private route: Router, private cookieservice: CookieService){
    }

    ngOnInit(){
        if(!this.cookieservice.get("loginuserMerck")){
            this.route.navigate(['/auth']);
        }
        this.getpayment();
        this.PayementSearch = new FormGroup({
            search: new FormControl("", [Validators.required]),
        });
    }

    getpayment(){
        this.appservice.paymentdata(this.startpagevalue,10)
        .subscribe(
            data=>{
                console.log(data);
                if(data.status=='1'){
                    this.paymentData = data.data.bookinginfo;
                    this.totalRecords = data.record;
                    this.itemsPerPage =10;
                    this.currentPage =this.initialPageValue;
                }
            }
        );
    }
    handleChange(en){
        this.pno=en-1;
        this.appservice.category(this.pno,10)
        .subscribe(
            data => {
                if(data.status=='1'){
                    console.log(data.data.category);
                    this.paymentData = data.data.category;
                    this.totalRecords = data.record;
                    this.itemsPerPage =10;
                    this.currentPage =en;
                }
            }
        );
    }
    getrecord(){
        this.totalrecord=document.getElementById("getpage");
        this.appservice.category(this.startpagevalue,this.totalrecord.value)
        .subscribe(
            data => {
                if(data.status=='1'){
                    this.paymentData = data.data.category;
                    if(this.totalrecord.value == 10){
                        this.totalRecords = data.record;
                    }else{
                        this.totalRecords = '';
                    }
                    this.itemsPerPage =10;
                    this.currentPage =this.initialPageValue;
                }
            }
        );
    }
}