import { Component, OnInit } from '@angular/core';
import {AppService} from '../../shared/services/service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import {ActivatedRoute ,Router} from '@angular/router';
import {ToasterService} from '../../shared/services/toaster.service';
import { LoaderService } from 'app/shared/services/loader.service';
import { CookieService } from 'ngx-cookie';

@Component({
    selector: 'app-post',
    templateUrl: './create-post.component.html',
    styleUrls: ['./create-post.component.scss']
})

export class CreatePostComponent implements OnInit{
    showbutton: boolean;
    id: any;
    PostForm: FormGroup;
    showErrorMsg: string;
    Sluglist: any;
    public editorValue: string = '';
    file: any;
    fileName: any;
    name: any;
    descen: any;
    categorydata :any;
   constructor(private appservice: AppService,private router: Router, private route: ActivatedRoute, private toasterservice: ToasterService, private loaderservice: LoaderService, private cookieservice: CookieService  ){}
    
    ngOnInit(){
        if(!this.cookieservice.get("loginuserMerck")){
            this.router.navigate(['/auth']);
        }
        this.id = this.route.snapshot.paramMap.get('id');
        if(this.id){
            this.getpostdata();
            this.getCategory();
            this.showbutton = true;
            this.descen="";
            console.log(this.id);
            this.PostForm = new FormGroup({
                title: new FormControl("", [Validators.required]),
                description: new FormControl("", [Validators.required]),
                budget: new FormControl("", [Validators.required]),
                team: new FormControl("", [Validators.required]),
                category: new FormControl("", [Validators.required]),
            });
        }
    }
    getCategory(){
        this.appservice.category(1,1000)
        .subscribe(
            data=>{
                if(data.status=='1'){
                    this.categorydata = data.data.category;
                }
            }
        );
    }
    getpostdata(){
        this.appservice.PostDataid(this.id)
          .subscribe(
              data=>{
                  console.log(data);
                  if(data.status=='1')
                  {
                      this.showErrorMsg = "";
                      this.descen=data.data[0];
                      this.PostForm = new FormGroup({
                        title: new FormControl(data.data.postinfoid[0].title, [Validators.required]),
                        description: new FormControl(data.data.postinfoid[0].description, [Validators.required]),
                        budget: new FormControl(data.data.postinfoid[0].budget, [Validators.required]),
                        team: new FormControl(data.data.postinfoid[0].team, [Validators.required]),
                        category: new FormControl(data.data.postinfoid[0].category, [Validators.required]),
                        image: new FormControl(data.data.postinfoid[0].image, [Validators.required]),
                      });
                      
                  }else{
                      this.showbutton = false;
                  }
              }
          );
    }

    filechange(e){
        this.file = e.target.files[0];
        console.log(this.file);
        this.fileName = e.target.files[0];
        this.name = e.target.files[0].name;
    }
    createblogdata(image){
        if(this.PostForm.invalid){
            return;
        }    
        if(this.PostForm.valid){
            console.log(this.PostForm);
            this.loaderservice.display(true);
            this.appservice.addPostdata( this.PostForm.value, this.fileName)
            .subscribe(
            data=>{
                console.log(data);
                if(data.status=='1')
                {
                    this.toasterservice.Success(data.message);   
                    this.router.navigate(['/post']);                
                }else if(data.status=='2'){
                    this.toasterservice.Error(data.message);
                    this.router.navigate(['/post']);  
                }else{
                    this.toasterservice.Error(data.message);
                }
            }
        );
            //console.log(this.UserForm.value);
        }
    }
    editpostdata(){
        if(this.PostForm.invalid){
            return;
        }    
        if(this.PostForm.valid){
            this.loaderservice.display(true);
            this.appservice.editpostdata(this.PostForm.value,this.id,this.fileName)
            .subscribe(
                data=>{
                    console.log(data);
                    if(data.status=='1')
                    {
                        this.toasterservice.Success(data.message);   
                        this.router.navigate(['/post']);                
                    }else if(data.status=='2'){
                        this.toasterservice.Error(data.message);
                        this.router.navigate(['/post']);  
                    }else{
                        this.toasterservice.Error(data.message);
                    }
                }
            );
        }
    }
    Gotolist(){
        this.router.navigate(['/post']);
    }

}


